import { withRouter } from "react-router";
import React, { useEffect, useState } from "react";
import UdemyCoursesData from '@data/courses';
import { urlFromItem } from '@components/Courses/index';
const courses = {};
const CourseUrls = {};
UdemyCoursesData.forEach((course, index) => {
    const computedUrl = urlFromItem(course);
    courses[course.id] = {
        ...course,
        computedUrl: computedUrl
    };
    CourseUrls[course.id] = computedUrl
});
function CourseLoader(props) {
    const [loaded, setLoaded] = useState<boolean>(false);
    useEffect(() => {
        const key = props.match.params.key;
        if (CourseUrls?.[key] !== undefined) {
            window.location.href = CourseUrls[key];
        } else {
            window.location.href = Object.keys(CourseUrls)[0];
        }
        setLoaded(true);
    }, [])
    if (!loaded) {
        return <div>Loading...</div>;
    }
    return (
        <div style={{
            width: "100vw",
            height: "calc(100vh - 250px)",
            display: "flex",
            justifyContent: "center",
            paddingTop: "70px",
            backgroundColor: "#2D2D2D"
        }}>
            <div className="d-flex justify-content-center align-items-center">
                <div className="spinner-border text-light" style={{
                    width: "8rem",
                    height: "8rem",
                    borderWidth: "3px"
                }} role="status">
                    <span className="visually-hidden">Loading...</span>
                </div>
            </div>
        </div>
    )
}
export default withRouter(CourseLoader);