import Helmet from "react-helmet"

export type SeoSettingsType = {
    title: string,
    description: string,
    keywords: string,
    author: string,
    image: string | any,
    locale: string,
    siteName: string
}
export const DefaultSeoSettings: SeoSettingsType = {
    title: "Can Gökçeaslan | Kurucu Ortak & FullStack Developer",
    description: "Merhabalar, ben Can Gökçeaslan. Şu an bir girişimci ve online eğitmenim. Benimle ilgili daha fazla bilgiye sahip olabilmek için lütfen sayfayı ziyaret edin.",
    keywords: "cangokceaslan, Can Gökçeaslan, Can Gokceaslan, CanGokceaslan, Node.js, NodeJs, JavaScript, Fullstack Developer, CoFounder, Girişim",
    author: "Can Gökçeaslan",
    image: "/assets/jpg/social.jpg",
    locale: "tr",
    siteName: "https://www.cangokceaslan.com"
}
const DocumentSettings = ({
    docData = DefaultSeoSettings
}) => {
    const image = "/assets/jpg/social.jpg";

    return (
        <Helmet>
            <title>{docData?.title}</title>
            <meta name="description" content={docData?.description} />
            <meta name="keywords" content={docData?.keywords} />
            <meta name="author" content={docData?.author} />
            <meta name="image" content={docData?.image || image} />
            <meta name="locale" content={docData?.locale} />
            <meta name="siteName" content={docData?.siteName} />
            <meta name="og:title" content={docData?.title} />
            <meta name="og:description" content={docData?.description} />
            <meta name="og:image" content={docData?.image || image} />
            <meta name="og:locale" content={docData?.locale} />
            <meta name="og:site_name" content={docData?.siteName} />
            <meta name="twitter:title" content={docData?.title} />
            <meta name="twitter:description" content={docData?.description} />
            <meta name="twitter:image" content={docData?.image || image} />
            <meta name="twitter:card" content="summary_large_image" />
            <meta name="twitter:site" content={docData?.siteName} />
            <meta name="twitter:creator" content={docData?.author} />
        </Helmet>
    )
}

export default DocumentSettings;